/* eslint no-use-before-define: ["error", { "variables": false }] */

import PropTypes from 'prop-types';
import React from 'react';
import MessageText from './MessageText';
import MessageImage from './MessageImage';
import MessageVideo from './MessageVideo';

import Time from './Time';
import Color from './Color';

import { isSameUser, isSameDay } from './utils';
import { Typography, Button } from '@material-ui/core';

const styles = {
  left: {
    container: {
      flex: 1,
      alignItems: 'flex-start',
    },
    wrapper: {
      borderRadius: 15,
      backgroundColor: Color.leftBubbleBackground,
      marginRight: 60,
      minHeight: 20,
      justifyContent: 'flex-end',
    },
    containerToNext: {
      borderBottomLeftRadius: 3,
    },
    containerToPrevious: {
      borderTopLeftRadius: 3,
    },
    bottom: {
      flexDirection: 'row',
      justifyContent: 'flex-start',
    },
  },
  right: {
    container: {
      flex: 1,
      alignItems: 'flex-end',
    },
    wrapper: {
      borderRadius: 15,
      backgroundColor: Color.defaultBlue,
      marginLeft: 60,
      minHeight: 20,
      justifyContent: 'flex-end',
    },
    containerToNext: {
      borderBottomRightRadius: 3,
    },
    containerToPrevious: {
      borderTopRightRadius: 3,
    },
    bottom: {
      flexDirection: 'row',
      justifyContent: 'flex-end',
    },
  },
  tick: {
    fontSize: 10,
    backgroundColor: Color.backgroundTransparent,
    color: Color.white,
  },
  tickView: {
    flexDirection: 'row',
    marginRight: 10,
  },
  username: {
    top: -3,
    left: 0,
    fontSize: 12,
    backgroundColor: 'transparent',
    color: '#aaa',
  },
  usernameView: {
    flexDirection: 'row',
    marginHorizontal: 10,
  },
};

export default class Bubble extends React.Component {

  handleBubbleToNext() {
    if (
      isSameUser(this.props.currentMessage, this.props.nextMessage) &&
      isSameDay(this.props.currentMessage, this.props.nextMessage)
    ) {
      return ({
        ...styles[this.props.position].containerToNext,
        ...this.props.containerToNextStyle[this.props.position],
      });
    }
    return null;
  }

  handleBubbleToPrevious() {
    if (
      isSameUser(this.props.currentMessage, this.props.previousMessage) &&
      isSameDay(this.props.currentMessage, this.props.previousMessage)
    ) {
      return ({
        ...styles[this.props.position].containerToPrevious,
        ...this.props.containerToPreviousStyle[this.props.position],
      });
    }
    return null;
  }

  renderMessageText() {
    if (this.props.currentMessage.text) {
      const { containerStyle, wrapperStyle, ...messageTextProps } = this.props;
      if (this.props.renderMessageText) {
        return this.props.renderMessageText(messageTextProps);
      }
      return <MessageText {...messageTextProps} />;
    }
    return null;
  }

  renderMessageImage() {
    if (this.props.currentMessage.image) {
      const { containerStyle, wrapperStyle, ...messageImageProps } = this.props;
      if (this.props.renderMessageImage) {
        return this.props.renderMessageImage(messageImageProps);
      }
      return <MessageImage {...messageImageProps} />;
    }
    return null;
  }

  renderMessageVideo() {
    if (this.props.currentMessage.video) {
      const { containerStyle, wrapperStyle, ...messageVideoProps } = this.props;
      if (this.props.renderMessageVideo) {
        return this.props.renderMessageVideo(messageVideoProps);
      }
      return <MessageVideo {...messageVideoProps} />;
    }
    return null;
  }

  renderTicks() {
    const { currentMessage } = this.props;
    if (this.props.renderTicks) {
      return this.props.renderTicks(currentMessage);
    }
    if (currentMessage.user.id !== this.props.user.id) {
        return;
    }
    if (currentMessage.sent || currentMessage.received || currentMessage.pending) {
      return (
        <div style={{...styles.tickView}}>
          {currentMessage.sent && <Typography style={{...styles.tick, ...this.props.tickStyle}}></Typography>}
          {currentMessage.received && <Typography style={{...styles.tick, ...this.props.tickStyle}}></Typography>}
          {currentMessage.pending && <Typography style={{...styles.tick, ...this.props.tickStyle}}></Typography>}
        </div>
      );
    }
    return null;
  }

  renderTime() {
    if (this.props.currentMessage.createdAt) {
      const { containerStyle, wrapperStyle, ...timeProps } = this.props;
      if (this.props.renderTime) {
        return this.props.renderTime(timeProps);
      }
      return <Time {...timeProps} />;
    }
    return null;
  }

  renderUsername() {
    const { currentMessage } = this.props;
    if (this.props.renderUsernameOnMessage) {
      if (currentMessage.user.id === this.props.user.id) {
        return null;
      }
      return (
        <div style={{...styles.usernameView}}>
          <Typography style={{...styles.username, ...this.props.usernameStyle}}>~ {currentMessage.user.name}</Typography>
        </div>
      );
    }
    return null;
  }

  renderCustomView() {
    if (this.props.renderCustomView) {
      return this.props.renderCustomView(this.props);
    }
    return null;
  }

  render() {
    return (
      <div style={{...styles[this.props.position].container, ...this.props.containerStyle[this.props.position]}}>
        <div
          style={{
            ...styles[this.props.position].wrapper,
            ...this.props.wrapperStyle[this.props.position],
            ...this.handleBubbleToNext(),
            ...this.handleBubbleToPrevious(),
          }}
        >
          <Button
            onClick={this.onLongPress}
            {...this.props.touchableProps}
          >
            <div>
              {this.renderCustomView()}
              {this.renderMessageImage()}
              {this.renderMessageVideo()}
              {this.renderMessageText()}
              <div style={{...styles[this.props.position].bottom, ...this.props.bottomContainerStyle[this.props.position]}}>
                {this.renderUsername()}
                {this.renderTime()}
                {this.renderTicks()}
              </div>
            </div>
          </Button>
        </div>
      </div>
    );
  }

}



Bubble.contextTypes = {
  actionSheet: PropTypes.func,
};

Bubble.defaultProps = {
  touchableProps: {},
  onLongPress: null,
  renderMessageImage: null,
  renderMessageVideo: null,
  renderMessageText: null,
  renderCustomView: null,
  renderUsername: null,
  renderTicks: null,
  renderTime: null,
  position: 'left',
  currentMessage: {
    text: null,
    createdAt: null,
    image: null,
  },
  nextMessage: {},
  previousMessage: {},
  containerStyle: {},
  wrapperStyle: {},
  bottomContainerStyle: {},
  tickStyle: {},
  usernameStyle: {},
  containerToNextStyle: {},
  containerToPreviousStyle: {},
};

Bubble.propTypes = {
  user: PropTypes.object.isRequired,
  touchableProps: PropTypes.object,
  onLongPress: PropTypes.func,
  renderMessageImage: PropTypes.func,
  renderMessageVideo: PropTypes.func,
  renderMessageText: PropTypes.func,
  renderCustomView: PropTypes.func,
  renderUsernameOnMessage: PropTypes.bool,
  renderUsername: PropTypes.func,
  renderTime: PropTypes.func,
  renderTicks: PropTypes.func,
  position: PropTypes.oneOf(['left', 'right']),
  currentMessage: PropTypes.object,
  nextMessage: PropTypes.object,
  previousMessage: PropTypes.object,
  // containerStyle: PropTypes.shape({
  //   left: ViewPropTypes.style,
  //   right: ViewPropTypes.style,
  // }),
  // wrapperStyle: PropTypes.shape({
  //   left: ViewPropTypes.style,
  //   right: ViewPropTypes.style,
  // }),
  // bottomContainerStyle: PropTypes.shape({
  //   left: ViewPropTypes.style,
  //   right: ViewPropTypes.style,
  // }),
  // tickStyle: Text.propTypes.style,
  // usernameStyle: Text.propTypes.style,
  // containerToNextStyle: PropTypes.shape({
  //   left: ViewPropTypes.style,
  //   right: ViewPropTypes.style,
  // }),
  // containerToPreviousStyle: PropTypes.shape({
  //   left: ViewPropTypes.style,
  //   right: ViewPropTypes.style,
  // }),
};
